import { createContext, useEffect, useState } from "react";

const MoviesContext = createContext();

export function MoviesContextProvider({ children }) {
  //Array s filmy, do kt. se budou p≈ôid√°vat
  const [movies, setMovies] = useState([]);
  const [displayedDetail, setDisplayedDetail] = useState("");
  //Sleduje ID filmu, na kter√Ω se kliknulo - aby pak ≈°el identifikovat a≈æ se bude mƒõnit detail
  const [clickedMovieId, setClickedMovieId] = useState("");

  //State, kter√©mu se p≈ôidƒõluj√≠ 3 hodnoty a podle nich se podm√≠nƒõnƒõ renderujou vƒõci v mod√°ln√≠m oknƒõ (comp. MovieDetail).
  //Tyto hodnoty m≈Ø≈æe state m√≠t:
  // "DISPLAY-DETAIL"
  // "DISPLAY-TEXT_AREA"
  // "DISPLAY-NO_DETAIL"
  const [detailState, setDetailState] = useState("");

  //Initial naƒçten√≠ a zobrazen√≠ z localStorage
  useEffect(() => {
    const localData = localStorage.getItem("movies");
    if (localData) {
      setMovies(JSON.parse(localData));
    }
  }, []);

  //Ukl√°d√°n√≠ movies do localStorage
  useEffect(() => {
    localStorage.setItem("movies", JSON.stringify(movies));
  }, [movies]);

  //P≈ôid√°n√≠ filmu do array
  const addToMovies = (addedMovie) => {
    const movieToBeAdded = {
      id: addedMovie.id,
      title: addedMovie.title,
      movieYear: addedMovie.movieYear,
      imageURL: addedMovie.imageURL,
      dateWatched: addedMovie.dateWatched,
      stars_1: 0,
      stars_2: 0,
      totalRating: null,
      detail: "",
    };

    setMovies((prevMovies) => {
      return [movieToBeAdded, ...prevMovies];
    });
  };

  //Odstranƒõn√≠ filmu po kliknut√≠ na ikonu.
  const deleteMovie = (movieId) => {
    setMovies((prevMovies) => {
      const updatedMovies = prevMovies.filter((movie) => movie.id !== movieId);
      return updatedMovies;
    });
  };

  const onChangeDisplayedDetail = () => {
    const detailToBeShown = movies.find(
      (movie) => movie.id === clickedMovieId
    ).detail;

    setDisplayedDetail(detailToBeShown);
  };

  return (
    <MoviesContext.Provider
      value={{
        movies,
        setMovies,
        addToMovies,
        deleteMovie,
        displayedDetail,
        setDisplayedDetail,
        setClickedMovieId,
        clickedMovieId,
        onChangeDisplayedDetail,
        setDetailState,
        detailState,
      }}
    >
      {children}
    </MoviesContext.Provider>
  );
}
export default MoviesContext;
